<template>
  <v-row justify="center">
    <v-dialog v-model="browseStates" dark scrollable persistent max-width="600">
      <v-card>
        <v-card-title
          class="text-h6 grey--text text--lighten-1 font-weight-light"
        >
          Select ioBroker State
        </v-card-title>

        <v-divider></v-divider>
        <v-card-text style="height: 300px">
          <v-treeview
            :active.sync="active"
            :items="items"
            :load-children="getStates"
            :open.sync="open"
            activatable
            color="primary"
            open-on-click
            transition
            
          >
            <template v-slot:prepend="{ item, open }">
              <v-icon v-if="item.children">
                {{ open ? "mdi-folder-open" : "mdi-folder" }}
              </v-icon>
              <v-icon v-else> mdi-record </v-icon>
                {{ item.friendly_name }}  
            </template>
          </v-treeview>
        </v-card-text>
        <v-divider></v-divider>

         <v-text-field 
            v-model="selectedId"
            label="Selected State ID"
            readonly
            class="mt-4 ml-4 mr-4"
          ></v-text-field>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="close(false)">
            Cancel
          </v-btn>
          <v-btn color="green darken-1" text :disabled="!selectedId"  @click="close(true)"> Ok </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import mixins from "../mixins";
import { mapState } from "vuex";
import backend from "../api/backend";

export default {
  name: "browse-states",
  mixins: [mixins],
  data() {
    return {
      active: [],
      open: [],
      states: [],
    };
  },
  props: {
    source: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  computed: {
    items() {
      return [
        {
          id: 1,
          friendly_name: "ioBroker States",
          children: this.states,
        },
      ];
    },
    selectedId() {
      return this.active.length > 0 ? this.active[0] : '';
    },
    ...mapState({
      browseStates: (state) => state.browseStates,
      // states: (state) => state.states,
    }),
    selected() {
      function find (tree, value, key = 'id') {
        const stack = [tree[0]]
        while (stack.length) {
          const node = stack.shift()
          if (node[key] === value) return node
          node.children && stack.push(...node.children)
        }
        return null
      }
      if (!this.active.length) return undefined;
      const id = this.active[0];
      // console.log(id);
      let state = find(this.items, id)
      // console.log(JSON.stringify(state));
      return state;
    },
  },

  methods: {
    close(applySelection) {
      // console.log(applySelection);
      this.$store.commit("closeBrowseStateDialog");
      if (applySelection) {
        this.dispatch("capability", "state-selected", this.selected);
      }
    },
    async getStates(item) {
      if (item.children.length > 0) {
        return;
      }
      return backend
        .getStates()
        .then((respond) => {
          item.children.push(...respond.children);
        })
        .catch((err) => console.warn(err));
      // this.$store.dispatch("getStates");
    },
  },
};
</script>
