<template>
  <div>
    <v-container class="fill-height ma-0 pa-0 scrollbar" fluid>
      <confirmation />
      <v-layout class="split">
        <section class="left">
          <v-card color="grey lighten-4" flat tile>
            <v-toolbar dense>
              <v-btn icon @click="confirmSave">
                <v-icon>mdi-content-save</v-icon>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn icon @click="addEndpoint">
                <v-icon color="green lighten-2">mdi-plus</v-icon>
              </v-btn>
              <v-btn icon @click="removeEndpoint">
                <v-icon color="red lighten-2">mdi-delete</v-icon>
              </v-btn>
            </v-toolbar>
          </v-card>

          <v-treeview
            class="font-weight-regular"
            :active.sync="active"
            :items="items"
            :load-children="getItems"
            :open.sync="open"
            activatable
            color="primary"
            open-on-click
            transition
          >
            <template v-slot:prepend="{ item }">
              <v-icon v-if="!item.children"> mdi-record </v-icon>
              <v-icon
                v-if="item.children"
                v-text="
                  `mdi-${item.id === 1 ? 'home-variant' : 'folder-network'}`
                "
              ></v-icon>
              {{ item.friendly_name }}
            </template>
          </v-treeview>
        </section>
        <section class="right">
          <div>
            <!-- <v-divider vertical></v-divider> -->

            <v-scroll-y-transition mode="out-in">
              <div
                v-if="!selected"
                class="text-h6 grey--text text--lighten-1 font-weight-light"
                style="align-self: center"
              >
                Select a Device
              </div>
              <div v-else :key="selected.id" class="pt-3 mx-auto" flat>
                <div>
                  <v-img
                    :src="this.deviceIcon()"
                    class="mb-6"
                    height="88"
                    contain
                  ></v-img>
                  <h3 class="text-h5 mb-2 white--text">
                    {{ selected.friendly_name }}
                  </h3>
                  <div class="blue--text subheading font-weight-bold">
                    {{ selected.display_categories }}
                  </div>
                </div>

                <v-divider class="mt-4 mb-0"></v-divider>

                <v-row class="text-left ma-0 pa-2">
                  <v-col class="ma-0 pa-2">
                    <p
                      class="text-h6 grey--text mt-1 mb-0 text--lighten-1 font-weight-light"
                    >
                      Name
                    </p>

                    <v-text-field v-model="selected.friendly_name" />
                  </v-col>
                </v-row>
                <v-row class="text-left ma-0 pa-2">
                  <v-col class="ma-0 pa-2">

                  
                    <p
                      class="text-h6 grey--text mt-1 mb-2 text--lighten-1 font-weight-light"
                    >
                      Display Categories
                    </p>
                    <v-chip-group
                      multiple
                      mandatory
                      column
                      active-class="primary"
                      v-model="selected.display_categories"
                    >
                      <v-chip v-for="tag in categories" :key="tag" :value="tag">
                        {{ tag }}
                      </v-chip>
                    </v-chip-group>
                  </v-col>
                </v-row>

                <v-row class="text-left ma-0 pa-2">
                  <v-col class="ma-0 pa-2">
                    <p
                      class="text-h6 grey--text mt-1 mb-0 text--lighten-1 font-weight-light"
                    >
                      Capabilities
                    </p>
                    <v-combobox
                      v-model="selectedCapabilities"
                      placeholder="Select device capabilities from the list"
                      :items="availableCapabilities"
                      :item-text="'friendly_name'"
                      :item-value="'friendly_name'"
                      return-object
                      hide-selected
                      multiple
                    >
                      <template
                        v-slot:selection="{ attrs, item, parent, selected }"
                      >
                        <v-chip
                          :key="item.friendly_name"
                          v-bind="attrs"
                          :color="`${item.color} lighten-1`"
                          :input-value="selected"
                          label
                          small
                        >
                          <span class="pr-2">
                            {{ item.friendly_name }}
                          </span>
                          <v-icon small @click="parent.selectItem(item)">
                            $delete
                          </v-icon>
                        </v-chip>
                      </template>
                      <template v-slot:item="{ item }">
                        <v-chip
                          :color="`${item.color} lighten-1`"
                          dark
                          label
                          small
                        >
                          {{ item.friendly_name }}
                        </v-chip>
                        <v-spacer></v-spacer>
                      </template>
                    </v-combobox>
                  </v-col>
                </v-row>

                <v-row class="text-left ma-0 pa-2">
                  <v-col class="ma-0 pa-2">
                    <v-expansion-panels multiple>
                      <v-expansion-panel
                        v-for="item in selected.capabilities"
                        :key="item.namespace"
                      >
                        <v-expansion-panel-header>
                          <v-chip
                            :color="`${capabilityColor(item.namespace)}`"
                            dark
                            label
                            small
                          >
                            {{ capabilityName(item.namespace) }}
                          </v-chip>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                          <capability :source="item" />
                        </v-expansion-panel-content>
                      </v-expansion-panel>
                    </v-expansion-panels>
                  </v-col>
                </v-row>
              </div>
            </v-scroll-y-transition>
          </div>
        </section>

        <v-snackbar v-model="snackbar" timeout="4000" :color="snackbarColor">
          {{ snackbarText }}
          <template v-slot:action="{ attrs }">
            <v-btn color="black" text v-bind="attrs" @click="snackbar = false">
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-layout>
    </v-container>
  </div>
</template>

<style>
/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #424242 #121212;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 6px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #424242;
  border-radius: 20px;
  border: 3px solid transparent;
}

.left {
  overflow-y: auto;
  width: 30%;
}
.right {
  flex: 1;
  overflow-y: auto;
  border-left: solid;
  border-color: darkgray;
  border-width: 1px;
}

.split {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
</style>

<script>
import { mapState } from "vuex";
// import { mapGetters } from "vuex";
import Capability from "../components/Capability.vue";
import ConfirmationDialog from "../components/ConfirmationDialog.vue";

import backend from "../api/backend";

export default {
  name: "endpoints-overview",
  data() {
    return {
      endpoints: [],
      active: [],
      open: [],
      selectedCapabilities: [],
      snackbar: false,
      snackbarText: "",
      snackbarColor: "red",
      categories: ["SWITCH", "LIGHT", "SMARTLOCK", "MOTION_SENSOR", "DOORBELL", "OTHER"],
    };
  },
  components: { capability: Capability, confirmation: ConfirmationDialog },
  methods: {
    async getItems() {
      let self = this;
      return backend
        .getEndpoints()
        .then((respond) => {
          self.endpoints.push(...respond.endpoints);
        })
        .catch((err) => console.warn(err));
    },

    toast(message, isError = true) {
      this.snackbarText = message;
      this.snackbarColor = isError ? "red" : "green";
      this.snackbar = true;
    },

    validateEndpoints() {
      function flatten(arr) {
        return arr.reduce(function (flat, toFlatten) {
          return flat.concat(
            Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten
          );
        }, []);
      }

      let valid = true;
      let message = "";
      if (this.endpoints.length == 0) {
        return true;
      }

      for (let endpoint of this.endpoints) {
        if (endpoint.friendly_name.length == 0) {
          message = "Empty device names are not allowed";
          valid = false;
          break;
        }

        if (endpoint.capabilities.length == 0) {
          message = `A device must have at least one capability (${endpoint.friendly_name})`;
          valid = false;
          break;
        }

        for (let capability of endpoint.capabilities) {
          let prop = capability.properties[0];
          if (prop.io_address.length == 0) {
            message = `A capability must have an ioBroker state set (${endpoint.friendly_name} - ${capability.friendly_name})`;
            valid = false;
            break;
          }
          if (prop.values.length > 0) {
            let ioValues = prop.values.map((v) => v.io_value);
            let unique = ioValues.filter((v, i, a) => a.indexOf(v) === i);
            if (ioValues.length != unique.length) {
              message = `Values mapping must contain unique values (${endpoint.friendly_name} - ${capability.friendly_name})`;
              valid = false;
              break;
            }
          }
        }
        if (!valid) {
          break;
        }
      }

      let endpointNames = this.endpoints.map((e) => e.friendly_name);
      let uniqueEndpointNames = endpointNames.filter(
        (v, i, a) => a.indexOf(v) === i
      );
      if (endpointNames.length != uniqueEndpointNames.length) {
        message = `Endpoint names must be unique`;
        valid = false;
      }

      let states = flatten(
        flatten(
          flatten(this.endpoints.map((e) => e.capabilities)).map(
            (c) => c.properties
          )
        )
      ).map((p) => p.io_address);
      let s = new Set(states);
      if (states.length != s.size) {
        message = `Selected ioBroker states must be unique`;
        valid = false;
      }

      if (!valid) {
        this.toast(message);
      }

      return valid;
    },

    confirmSave() {
      if (this.validateEndpoints()) {
        this.$store.commit("openConfirmationDialog");
      }
    },

    addEndpoint() {
      function uuidv4() {
        return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
          (
            c ^
            (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
          ).toString(16)
        );
      }

      const endp = {
        id: uuidv4(),
        friendly_name: "New Device",
        capabilities: [],
        display_categories: ["OTHER"],
      };
      this.endpoints.push(endp);
      this.open = [1];
      this.active = [endp.id];
    },
    removeEndpoint() {
      let sel = this.selected;
      if (sel) {
        let index = this.endpoints.indexOf(sel);
        if (index > -1) {
          this.endpoints.splice(index, 1);

          if (this.endpoints.length > 0) {
            // console.log(`size: ${this.endpoints.length}, index: ${index}`)
            index = index > 0 ? (index >= this.endpoints.length ? index - 1 : index ) : 0;
            // console.log(`set index to: ${index}`)
            this.active = [this.endpoints[index].id];
          }
        }
      }
    },
    capabilityColor(namespace) {
      let cap = this.availableCapabilities.filter(c => c.namespace == namespace)[0];
      if (cap) {
        return cap.color;
      }


      // console.log(`no color for ${namespace}`)

      // switch (namespace) {
      //   case "Alexa.PowerController":
      //   case "powerState":
      //     return "teal lighteen-1";
      //   case "Alexa.BrightnessController":
      //   case "brightness":
      //     return "orange lighteen-1";
      //   case "Alexa.LockController":
      //   case "lockState":
      //     return "green lighteen-1";
      //   case "Alexa.MotionSensor":
      //   case "detectionState":
      //     return "purple lighteen-1";
      // }
    },
    capabilityName(namespace) {
      let ac = this.availableCapabilities.filter(c => c.namespace == namespace)[0]
      return ac ? ac.friendly_name : namespace;
    },

    light() {
      return require("../assets/light.png");
    },
    lock() {
      return require("../assets/lock.png");
    },
    switch() {
      return require("../assets/switch.png");
    },
    motion() {
      return require("../assets/motion.png");
    },
    doorbell() {
      return require("../assets/doorbell.png");
    },

    isMotionSensor() {
      return (
        this.selected.capabilities.find(
          (c) => c.namespace == "Alexa.MotionSensor"
        ) != null
      );
    },

    isDoorBell() {
      return (
        this.selected.capabilities.find(
          (c) => c.namespace == "Alexa.DoorbellEventSource"
        ) != null
      );
    },


    isLight() {
      return (
        this.selected.capabilities.find(
          (c) => c.namespace == "Alexa.BrightnessController"
        ) != null
      );
    },

    isSmartLock() {
      return (
        this.selected.capabilities.find(
          (c) => c.namespace == "Alexa.LockController"
        ) != null
      );
    },

    isSwitch() {
      return (
        this.selected.capabilities.find(
          (c) => c.namespace == "Alexa.PowerController"
        ) != null
      );
    },

    deviceIcon() {
      let selected = this.selected;
      if (selected) {
        if (this.isLight()) {
          return this.light();
        } else if (this.isSmartLock()) {
          return this.lock();
        } else if (this.isSwitch()) {
          return this.switch();
        } else if (this.isMotionSensor()) {
          return this.motion()
        } else if (this.isDoorBell()) {
          return this.doorbell()
        }
      }
      return require("../assets/other.png");
    },
    updateEndpointCapabilities() {
      // add new caps to selected endpoint
      for (let cap of this.selectedCapabilities) {
        let existing = this.selected.capabilities.find(
          (e) => e.namespace == cap.namespace
        );
        if (!existing) {
          // console.log(`not found ${cap.namespace}`);

          let c = JSON.parse(JSON.stringify(cap));
          c.properties.forEach((p) => {
            p.io_address = "";
            p.io_type = "";

            if (p.values.length > 0) {
              let values = JSON.parse(JSON.stringify(p.values));
              p.values.length = 0;
              values.forEach((v) => {
                p.values.push({
                  value: v,
                  io_value: v,
                });
              });
            }
          });

          this.selected.capabilities.push(c);
        } else {
          // console.log(`found ${cap.namespace}`);
          existing = cap;
        }
      }
      // remove non-selected caps from current endpoint
      for (let i = 0; i < this.selected.capabilities.length; i++) {
        let cap = this.selected.capabilities[i];
        let existsInSlected = this.selectedCapabilities.find(
          (e) => e.namespace == cap.namespace
        );
        if (!existsInSlected) {
          this.selected.capabilities.splice(i, 1);
        }
      }
    },

    updateSelectedCapabilities() {
      this.selectedCapabilities.length = 0;

      if (!this.selected) {
        return;
      }

      // console.log(JSON.stringify(this.selected.capabilities))

      for (let cap of this.selected.capabilities) {
        let c = this.availableCapabilities.find(
          (ac) => ac.namespace == cap.namespace
        );
        if (c) {
          this.selectedCapabilities.push(c);
        }
      }
    },
  },
  computed: {
    items() {
      return [
        {
          id: 1,
          friendly_name: "Managed by iwg-vpn",
          children: this.endpoints,
        },
      ];
    },
    selected() {
      if (!this.active.length) return undefined;
      const id = this.active[0];
      let endpoint = this.endpoints.find((e) => e.id === id);
      return endpoint;
    },
    availableCapabilities() {
      let caps = JSON.parse(JSON.stringify(this.capabilities));
      // let self = this;
      // caps.forEach((c) => {
      //   c.color = self.capabilityColor(c.namespace);
      // });

      console.log(`${JSON.stringify(caps)}`)

      return caps;
    },
    ...mapState({
      capabilities: (state) => state.capabilities,
    }),
  },
  watch: {
    selected: function () {
      this.deviceIcon();
      this.updateSelectedCapabilities();
    },
    selectedCapabilities: function () {
      this.updateEndpointCapabilities();
      this.deviceIcon();
    },
  },
  created() {
    this.$store.dispatch("getCapabilities");

    this.$on("save-confirmed", async () => {
      // update backend
      let respond = await backend.postEndpoints({ endpoints: this.endpoints });
      if (respond instanceof Error) {
        this.toast("Failed saving devices");
      } else {
        this.toast("Saved", false);
      }
    });
  },
};
</script>
