<template>
  <div>
    <v-alert border="left" :color="this.capabilityColor()" colored-border type="info" elevation="2" class="grey--text">
      {{ infoText() }}
    </v-alert>

    <p class="text-h6 grey--text text--lighten-1 font-weight-light">
      ioBroker State
    </p>

    <v-text-field
      dense
      readonly
      :append-outer-icon="'mdi-database-search'"
      @click:append-outer="selectIoAddress"
      flat
      :rules="[(source.properties[0].io_address || '').toString().length > 0 || 'Required']"
      hide-details="auto"
      v-model="source.properties[0].io_address"
    ></v-text-field>
    <state-browse-dialog />
    <p
      class="text-h6 grey--text mt-10 text--lighten-1 font-weight-light"
      v-if="source.properties[0].values.length > 0"
    >
      Values Mapping
    </p>

    <v-simple-table v-if="source.properties[0].values.length > 0" dense>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left grey--text text--lighten-1 font-weight-light">
              Value
            </th>
            <th class="text-left grey--text text--lighten-1 font-weight-light">
              ioBroker Value
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, i) in source.properties[0].values" :key="i">
            <td
              style="width: 50%"
              class="text-left grey--text text--lighten-1 font-weight-light"
            >
              {{ item.value }}
            </td>
            <td class="text-right">
              <v-text-field
                dense
                reverse
                flat
                :rules="[
                  (item.io_value || '').toString().length > 0 || 'Required',
                ]"
                hide-details="auto"
                v-model="item.io_value"
              ></v-text-field>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </div>
</template>

<script>
import mixins from "../mixins";
import StateBrowseDialog from './StateBrowseDialog.vue';
// import Vue from "vue";
// Vue.component("StateBrowseDialog", StateBrowseDialog);
import { mapState } from "vuex";
export default {
  name: "capability",
  mixins: [mixins],
  components: { StateBrowseDialog: StateBrowseDialog },
  props: {
    index: {
      // index of current item
      type: Number,
    },
    source: {
      type: Object,
      default() {
        return {};
      },
    },
  },
    computed: {
    ...mapState({
      browseStates: (state) => state.browseStates,
    }),
  },
  methods: {
    infoText() {
      switch (this.source.namespace) {
        case "Alexa.PowerController":
          return "If a device supports the switch capability, then Alexa can set it to ON or OFF.\
                Please define here which ioBroker state should be written the values to. Please also define the values mapping, meaning \
                which ioBroker value corresponds to the ON and to the OFF value.";
        case "Alexa.BrightnessController":
          return "If a device supports the dimmer capability, then Alexa can set it to values from 0 to 100.\
                Please define here which ioBroker state should be written the values to. Note that the ioBroker state must accept integer values.";
        case "Alexa.LockController":
          return "If a device supports the lock capability, then it can be locked and unlocked. The device can also report to be in the JAMMED state,\
                meaning the lock can't transition to locked or unlocked because the locking mechanism is jammed.\
                Please define here which ioBroker state should be written the values LOCKED and UNLOCKED to. Please also define the values mapping, meaning \
                which ioBroker value corresponds to the LOCKED, UNLOCKED and JAMMED values.";
        case "Alexa.MotionSensor":
          return "If a device supports the motion-sensor capability it can sense physical movement in an area. The device reports either motion DETECTED or NOT_DETECTED. \
                Alexa can only read the device state, not set it. Please define the values mapping, meaning which ioBroker value corresponds to the DETECTED and NOT_DETECED values.";
        case "Alexa.DoorbellEventSource":
          return "If a device supports the doorbell capability it can raise doorbell events. When a doorbell event occurs, Alexa plays the doorbell announcement on the selected Echo devices. \
                Alexa uses doorbell events for announcements, mobile notifications, routines, and other use cases. No value mapping has to be provided for a doorbell. A value change of the selected ioBroker property is considered to be a doorbell event. Every change has a cool down period \
                of 15 seconds, meaning all value changes of the selected state within 15 seconds starting from the first change are treated as a single doorbell event.";
      }

      return "";
    },

    capabilityColor() {
        // console.log(this.$parent)
        let parent = this.parentByName('endpoints-overview')
        if (parent) {
            // console.log(`looking color for ${this.source.namespace}`)
            return parent.capabilityColor(this.source.namespace)
        }    
        return 'info'
    },    
    selectIoAddress() {
      // console.log('dialog')
      // console.log(this.$store.browseStates)
      this.$store.commit('openBrowseStateDialog')
    },
  },
  created() {
    this.$on("state-selected", (state) => {
      if (!state) {
        return
      }
      // console.log(JSON.stringify(state))

      this.source.properties[0].io_address = state.id
      this.source.properties[0].io_value_type = state.type
    });
  }
};
</script>
