<template>
  <v-row justify="center">
    <v-dialog v-model="confirmation" dark scrollable persistent max-width="600">
      <v-card>
        <v-card-title
          class="text-h6 grey--text text--lighten-1 font-weight-light"
        >
          Save
        </v-card-title>

        <v-divider></v-divider>
        <v-card-text class="text-left mt-2">
          Saving the devices will overwrite the existing ones and also reset
          their values. 
          <br/>Trigger the device discovery in Alexa App or by voice command afterwards.
          It might take some time until Alexa gets synchronized.
          <br/><br/>Save?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="close(false)">
            Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="close(true)"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import mixins from "../mixins";
import { mapState } from "vuex";

export default {
  name: "confirmation",
  mixins: [mixins],
  data() {
    return {
      active: [],
      open: [],
      states: [],
    };
  },
  props: {
    source: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  computed: {
    ...mapState({
      confirmation: (state) => state.confirmation,
    }),
  },

  methods: {
    close(confirmed) {
      // console.log(confirmed);
      this.$store.commit("closeConfirmationDialog");
      if (confirmed) {
        this.dispatch("endpoints-overview", "save-confirmed", null);
      }
    },
  },
};
</script>
